ADF,6BAEI,WAAA,CAAA,CCpNN,QACE,qBAAA,CACA,4BAAA,CAEA,qBACE,SAAA,CACA,aAAA,CACA,yBAHF,qBAII,SAAA,CACA,YAAA,CACA,sBAAA,CACA,6BAAA,CACA,QAAA,CAAA,CAIJ,mBACE,iBAAA,CACA,0BAAA,CACA,cAAA,CACA,eAAA,CACA,gBAAA,CACA,aAAA,CAEA,oCACE,kBAAA,CAGF,yBAZF,mBAaI,eAAA,CACA,eAAA,CACA,8BACE,gBAAA,CAAA","sourcesContent":["/* ПОДКЛЮЧЕНИЯ */\r\n\r\n@import \"/src/normalize/normalize\\ \\(1\\).css\";\r\n@import \"./scss/fonts.scss\";\r\n@import \"./scss/color.scss\";\r\n@import \"./scss/background-white.scss\";\r\n@import \"./scss/sidebar-menu.scss\";\r\n@import \"./scss/modal-window.scss\";\r\n@import \"./scss/general.scss\";\r\n@import \"./scss/header.scss\";\r\n@import \"./scss/section-main.scss\";\r\n@import \"./scss/swiper.scss\";\r\n@import \"./scss/section-brands.scss\";\r\n@import \"./scss/section-type.scss\";\r\n@import \"./scss/section-price.scss\";\r\n@import \"./scss/footer.scss\";\r\n","// /* ШРИФТ */\r\n\r\n@font-face {\r\n  font-family: \"TT Lakes\";\r\n  src: local(\"TT Lakes Regular\"), url(\"fonts/TTLakes-Regular.woff\") format(\"woff\"), url(\"fonts/TTLakes-Regular.eot\") format(\"embedded-opentype\");\r\n  font-weight: 400;\r\n  font-style: normal;\r\n  font-display: swap;\r\n}\r\n\r\n@font-face {\r\n  font-family: \"TT Lakes\";\r\n  src: local(\"TT Lakes Medium\"), url(\"fonts/TTLakes-Medium.woff\") format(\"woff\"), url(\"fonts/TTLakes-Medium.eot\") format(\"embedded-opentype\");\r\n  font-weight: 500;\r\n  font-style: normal;\r\n  font-display: swap;\r\n}\r\n\r\n@font-face {\r\n  font-family: \"TT Lakes\";\r\n  src: local(\"TT Lakes Bold\"), url(\"fonts/TTLakes-Bold.woff\") format(\"woff\"), url(\"fonts/TTLakes-Bold.eot\") format(\"embedded-opentype\");\r\n  font-weight: 700;\r\n  font-style: normal;\r\n  font-display: swap;\r\n}\r\n","/* БЕЛЫЙ ФОН */\r\n.background-white {\r\n  position: fixed;\r\n  width: 100%;\r\n  height: 100%;\r\n  background-color: $color-white;\r\n  opacity: 0;\r\n  z-index: -1;\r\n\r\n  &--active {\r\n    opacity: 0.9;\r\n    z-index: 2;\r\n    transition: opacity 0.6s ease-in-out, z-index 0.3s ease-in-out;\r\n  }\r\n}\r\n","$color-white: #fff;\r\n$color-main-background: #f8f8f8;\r\n$color-main-turquoise: #41f6d7;\r\n$color-stroke-turquoise: #b8ffec;\r\n$color-secondary-turquoise: #d9fff5;\r\n$color-stroke-grey: #eaeaea;\r\n$color-dark-grey: #b5b6bc;\r\n$color-main-pink: #ff3e79;\r\n$color-secondary-black: rgba(224, 224, 224, 0);\r\n$text-color-main-black: #1b1c21;\r\n$text-color-main-grey: #7e7e82;\r\n","/* БЛОК САЙДБАР МЕНЮ */\r\n// ОБЩЕЕ\r\n.sidebar-menu {\r\n  z-index: 2;\r\n  width: 320px;\r\n  position: fixed;\r\n  background-color: $color-white;\r\n  box-shadow: 16px 0px 52px 0px rgba(14, 24, 80, 0.2), -2px 0px 4px 0px rgba(69, 79, 126, 0.02);\r\n  overflow-y: auto;\r\n  left: -150%;\r\n  transition: left 500ms ease-in-out;\r\n\r\n  &--open {\r\n    height: 100%;\r\n    left: 0;\r\n  }\r\n\r\n  @media (min-width: 360px) {\r\n    min-width: 360px;\r\n  }\r\n\r\n  @media (min-width: 768px) {\r\n    min-width: 320px;\r\n    width: 320px;\r\n  }\r\n\r\n  @media (min-width: 1440px) {\r\n    display: block;\r\n    position: static;\r\n    box-shadow: none;\r\n    z-index: 1;\r\n  }\r\n\r\n  /* ШАПКА */\r\n  &__header {\r\n    padding: 18.73px 16px 0 16px;\r\n\r\n    @media (min-width: 360px) {\r\n      padding: 18.73px 24px 0 24px;\r\n    }\r\n\r\n    @media (min-width: 768px) {\r\n      padding: 18.73px 24px 0 24px;\r\n    }\r\n\r\n    @media (min-width: 1440px) {\r\n      padding-top: 34px;\r\n    }\r\n  }\r\n\r\n  &__header-container {\r\n    display: flex;\r\n    justify-content: space-between;\r\n    align-items: center;\r\n  }\r\n\r\n  &__header-container-part1 {\r\n    display: flex;\r\n    flex-direction: row;\r\n    gap: 16.73px;\r\n  }\r\n\r\n  &__header-container-part2 {\r\n    display: flex;\r\n    flex-direction: row;\r\n  }\r\n\r\n  &__header-button {\r\n    margin-top: 5.73px;\r\n\r\n    &:focus,\r\n    &:hover {\r\n      opacity: 0.6;\r\n      transition: 0.2s ease-in-out;\r\n      cursor: pointer;\r\n    }\r\n  }\r\n\r\n  /* НАВИГАЦИЯ */\r\n  &__navigation {\r\n    margin: 26px 0 24px 0;\r\n    padding: 0 16px 0 16px;\r\n\r\n    @media (min-width: 360px) {\r\n      margin-top: 24px;\r\n      padding: 0 24px 0 24px;\r\n    }\r\n\r\n    @media (min-width: 768px) {\r\n      margin: 50.27px 0 268px 0;\r\n    }\r\n  }\r\n\r\n  &__list {\r\n    display: flex;\r\n    flex-direction: column;\r\n    justify-content: space-between;\r\n    margin-left: -16px;\r\n    gap: 10px;\r\n\r\n    @media (min-width: 360px) {\r\n      margin-left: -24px;\r\n    }\r\n  }\r\n\r\n  &__item {\r\n    padding-top: 4px;\r\n    padding-bottom: 4px;\r\n\r\n    @media (min-width: 768px) {\r\n      padding-top: 7px;\r\n      padding-bottom: 7px;\r\n    }\r\n\r\n    &:focus,\r\n    &:hover {\r\n      content: \"\";\r\n      width: 4px;\r\n      background-color: $color-main-turquoise;\r\n      border-radius: 0px 10px 10px 0px;\r\n      white-space: nowrap;\r\n      transition: 0.2s ease-in-out;\r\n    }\r\n  }\r\n\r\n  &__link {\r\n    color: $text-color-main-black;\r\n    font-family: TT Lakes;\r\n    font-size: 16px;\r\n    font-style: normal;\r\n    font-weight: 500;\r\n    line-height: 150%;\r\n    letter-spacing: -0.2px;\r\n    cursor: pointer;\r\n    padding: 0 0 0 16px;\r\n\r\n    @media (min-width: 360px) {\r\n      padding: 0 0 0 24px;\r\n    }\r\n\r\n    &:focus,\r\n    &:hover {\r\n      color: $text-color-main-grey;\r\n      transition: 0.2s ease-in-out;\r\n    }\r\n  }\r\n\r\n  /* ПОДВАЛ */\r\n  &__footer {\r\n    padding: 0 16px 32px 16px;\r\n\r\n    @media (min-width: 360px) {\r\n      padding: 0 24px 32px 24px;\r\n    }\r\n  }\r\n\r\n  &__footer-list {\r\n    display: flex;\r\n    justify-content: flex-start;\r\n    gap: 16px;\r\n  }\r\n\r\n  &__footer-contact {\r\n    display: flex;\r\n    flex-direction: column;\r\n    justify-content: flex-start;\r\n    gap: 12px;\r\n    margin: 16px 0 8px 0;\r\n\r\n    @media (min-width: 768px) {\r\n      margin-bottom: 40px;\r\n    }\r\n  }\r\n\r\n  &__number {\r\n    font-family: TT Lakes;\r\n    font-size: 24px;\r\n    font-style: normal;\r\n    font-weight: 700;\r\n    cursor: pointer;\r\n  }\r\n\r\n  &__mail {\r\n    cursor: pointer;\r\n  }\r\n\r\n  &__language-list {\r\n    display: flex;\r\n    justify-content: flex-start;\r\n    gap: 7px;\r\n  }\r\n\r\n  &__language-button {\r\n    &--active {\r\n      color: text-color-main-grey;\r\n    }\r\n  }\r\n\r\n  &__language-button {\r\n    &:focus,\r\n    &:hover {\r\n      color: $text-color-main-grey;\r\n      cursor: pointer;\r\n      transition: 0.2s ease-in-out;\r\n    }\r\n  }\r\n\r\n  &__footer-button {\r\n    &:focus,\r\n    &:hover {\r\n      opacity: 0.6;\r\n      transition: 0.2s ease-in-out;\r\n      cursor: pointer;\r\n    }\r\n  }\r\n}\r\n\r\n/* ПОЯВЛЕНИЕ САЙДБАР МЕНЮ ПОСЛЕ НАЖАТИЯ НА КНОПКУ МЕНЮ */\r\n.button-menu--open {\r\n  height: 100%;\r\n  left: 0;\r\n}\r\n\r\n/* ПОЯВЛЕНИЕ САЙДБАР МЕНЮ ПОСЛЕ 1440 ПХ+ */\r\n.button-menu--close {\r\n  @media (min-width: 1440px) {\r\n    display: none;\r\n  }\r\n}\r\n","/* МОДАЛЬНОЕ ОКНО - ПОЗВОНИТЬ И ОБРАТНАЯ СВЯЗЬ */\r\n/* ОБЩЕЕ */\r\n.modal-window {\r\n  padding: 24px 16px 24px 16px;\r\n  position: fixed;\r\n  overflow-y: auto;\r\n  height: 100%;\r\n  top: 0;\r\n  right: -150%;\r\n  background-color: $color-white;\r\n  z-index: 3;\r\n  width: 288px;\r\n  transition: right 500ms ease-in-out;\r\n  box-shadow: 16px 0px 52px 0px rgba(14, 24, 80, 0.2), -2px 0px 4px 0px rgba(69, 79, 126, 0.02);\r\n\r\n  @media (min-width: 1440px) {\r\n    width: 520px;\r\n    display: flex;\r\n    justify-content: right;\r\n    padding: 0;\r\n    background-color: $color-secondary-black;\r\n    box-shadow: none;\r\n  }\r\n\r\n  &__wrapper {\r\n    @media (min-width: 1440px) {\r\n      width: 376px;\r\n      padding: 24px 32px 24px 32px;\r\n      box-shadow: 16px 0px 52px 0px rgba(14, 24, 80, 0.2), -2px 0px 4px 0px rgba(69, 79, 126, 0.02);\r\n      background: $color-white;\r\n      z-index: 3;\r\n    }\r\n  }\r\n}\r\n\r\n.modal {\r\n  &__title {\r\n    margin: 40px 0 24px 0;\r\n    color: $text-color-main-black;\r\n    font-family: TT Lakes;\r\n    font-size: 24px;\r\n    font-style: normal;\r\n    font-weight: 700;\r\n    line-height: 32px; /* 133.333% */\r\n    letter-spacing: -0.6px;\r\n\r\n    &::before {\r\n      content: \"\";\r\n      position: absolute;\r\n      width: 4px;\r\n      height: 40px;\r\n      top: 105px;\r\n      left: 0px;\r\n      border-radius: 0 10px 10px 0;\r\n      background-color: $color-main-turquoise;\r\n    }\r\n\r\n    @media (min-width: 1440px) {\r\n      margin-top: 16px;\r\n      &::before {\r\n        top: 35px;\r\n        left: 80px;\r\n      }\r\n    }\r\n  }\r\n\r\n  &__form {\r\n    display: flex;\r\n    flex-direction: column;\r\n  }\r\n\r\n  &__input {\r\n    min-width: 88%;\r\n    height: 22px;\r\n    padding: 12px 16px;\r\n    border-radius: 8px;\r\n    border: 1px solid $color-stroke-grey;\r\n    background: $color-white;\r\n    margin-bottom: 16px;\r\n    font-family: \"TTLakes\", Helvetica, sans-serif;\r\n    font-size: 16px;\r\n    font-weight: 500;\r\n    line-height: 150%;\r\n    letter-spacing: -0.2px;\r\n\r\n    &:hover,\r\n    &:focus {\r\n      border-color: $color-main-turquoise;\r\n      outline: 0;\r\n    }\r\n  }\r\n\r\n  &__information {\r\n    max-width: 288px;\r\n    margin-bottom: 18px;\r\n    color: text-color-main-grey;\r\n    font-family: TT Lakes;\r\n    font-size: 14px;\r\n    font-style: normal;\r\n    font-weight: 500;\r\n    line-height: 16px; /* 114.286% */\r\n  }\r\n\r\n  &__link {\r\n    color: $color-main-pink;\r\n    font-family: TT Lakes;\r\n    font-size: 14px;\r\n    font-style: normal;\r\n    font-weight: 500;\r\n    line-height: 16px;\r\n  }\r\n\r\n  &__button {\r\n    display: flex;\r\n    gap: 10px;\r\n    align-self: flex-end;\r\n    align-items: center;\r\n    min-width: 136px;\r\n    height: 32px;\r\n    padding: 4px 12px 4px 18px;\r\n    background-color: $color-main-pink;\r\n    